/* breakpoints */
.boxed {
  box-sizing: border-box;
  margin: 20px 0;
}

@media only screen and (min-width: 381px) and (max-width: 959px) {
  .boxed {
    padding: 10px;
  }
}

@media only screen and (max-width: 380px) {
  .boxed {
    padding: 10px;
  }
}

.boxed > div {
  max-width: 960px;
  margin: 0 auto !important;
}
